import  './index.css'
import React, { Fragment, useEffect, useState} from 'react'
import {Row, Modal, InputNumber, Divider} from 'antd';
export const CHARACTER1 =  () => {
    const [isModalOpen, setIsModalOpen] = useState(false)
    const showModal = () => {
        setIsModalOpen(true)
    }

    const handleOk = () => {
        setIsModalOpen(false)
    }
    const handleCancel = () => {
        setIsModalOpen(false)
    }
    const onInputChange =  value => {
        console.log('changed', value);
    }

    return (
        <Fragment>
            <div className={'chc_container'}>
                <div className={"character1"}>
                    <img onClick={showModal} src="https://raw.githubusercontent.com/Yan-Cam/blogPictureHost/main/ca1.png" alt=""/>
                </div>
                <Modal title="角色属性" open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
                    <Divider/>
                    <Row justify={"space-between"}>
                    <InputNumber prefix={"血量"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"护甲"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"攻击"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"法强"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    </Row>
                    <Divider/>
                    <Row justify={"space-between"}>
                    <InputNumber prefix={"范围"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"灼烧"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"抵御"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    <InputNumber prefix={"耐力"} min={1} max={100000} defaultValue={1} onChange={onInputChange} />
                    </Row>
                    <Divider/>
                </Modal>
            </div>
        </Fragment>
    )
}