import  './index.css'
import React, { Fragment, useEffect, useState} from 'react'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import { Button } from 'antd';
export const HOME =  () => {
    const history = useHistory()
    const handleClick = event => {
        const {innerText:text} = event.target

        switch (text) {
            case '开始对局': {
                history.push('/cardgame/start')
            }
            case '卡牌图鉴': {

            }
        }
    }
    return (
        <Fragment>
            <div className={'home_container'}>
                <div className={"home_btn_container"}>
                    <Button block size={"large"} type="primary">选择人数</Button>
                    <Button onClick={handleClick} block size={"large"} type="primary">开始对局</Button>
                    <Button block size={"large"} type="primary">卡牌图鉴</Button>
                </div>
            </div>
        </Fragment>
    )
}